<template>
	<a-modal v-model="visible" title="编辑新电影" on-ok="handleOk" :afterClose="handleCancel" width="800px" :dialog-style="{ top: '15px' }">
		<template slot="footer">
			<a-button key="back" @click="handleCancel">
				取消
			</a-button>
			<a-button key="submit" type="primary" :loading="buttonloading" @click="handleOk">
				提交
			</a-button>
		</template>
		<div>
			<a-form :form="form" :label-col="labelCol" :wrapper-col="wrapperCol" hide-required-mark>
				<div style="margin-top: -10px; display: flex; justify-content: space-around;">
					<div>
						<a-form-item label="电影名">
							<a-input class="inputFieldStyle" v-decorator="['name', {rules: [
								{ required: true, message: '    电影名不能为空' }],
								},
							]"
							 placeholder="请输入电影名" />
						</a-form-item>
						<a-form-item label="导演">
							<a-input class="inputFieldStyle" v-decorator="['directors', {rules: [
								{ required: true, message: '导演不能为空' }],
								},
							]"
							 placeholder="请输入导演" />
						</a-form-item>
						<a-form-item label="编剧">
							<a-input class="inputFieldStyle" v-decorator="['writers', {rules: [
								{ required: true, message: '编剧不能为空' }],
								},
							]"
							 placeholder="请输入编剧" />
						</a-form-item>
						<a-form-item label="演员">
							<a-input class="inputFieldStyle" v-decorator="['stars', {rules: [
								{ required: true, message: '演员不能为空' }],
								},
							]"
							 placeholder="请输入演员" />
						</a-form-item>
						<a-form-item label="播放时长">
							<a-input class="inputFieldStyle" v-decorator="['playTime', {rules: [
								{ required: true, message: '播放时长不能为空' }],
								},
							]"
							 placeholder="请输入片长" />
						</a-form-item>
						<a-form-item label="发行时间">
							<a-date-picker type="date" class="inputFieldStyle" v-decorator="['releaseTime', {rules: [{ required: true, message: '请选择发行时间' }],
								},
							]"
							 placeholder="选择一个日期"  @change="onChange"/>
						</a-form-item>
					</div>
					<a-form-item>
						<a-upload name="file" list-type="picture-card" class="poster-uploader" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadMoviePoster'"
						 :before-upload="beforeUpload" @change="handleChange">
							<img v-if="imageUrl" :src="imageUrl" style="width: 200px; height: 300px;" />
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-item>
				</div>
				<div style="margin: 10px 0 0 -30px;">
					<a-form-item label="类别">
						<a-checkbox-group v-decorator="['genre']" style="margin-left:20px;width: 80%;">
							<a-checkbox :value="index" :name="item" v-for="(item, index) in genreList" style="vertical-align:middle;">
								{{item}}
							</a-checkbox>
						</a-checkbox-group>
					</a-form-item>
					<a-form-item label="介绍">
						<a-input type="textarea" style="margin-left: 10px;" v-decorator="['intro', {rules: [
								{ required: true, message: '电影介绍不能为空' }],
								},
							]"
						 placeholder="请输入简介" />
					</a-form-item>
				</div>
			</a-form>
		</div>
	</a-modal>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath'])
		},
		props: ['visible'],
		data() {
			return {
				form: this.$form.createForm(this),
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				imageUrl: '',
				newRow: {
					"mid": null,
					"name": null,
					"genre": null,
					"releaseTime": null,
					"directors": null,
				},
				movie: {
					"name": "undefine",
					"playTime": "undefine",
					"releaseTime": "undefine",
					"genre": "undefine",
					"directors": "undefine",
					"writers": "undefine",
					"stars": "undefine",
					"intro": null,
					"pic": "default.jpg"
				},
				picloading: false,
				buttonloading: false,
				genreList: ['动作', '冒险', '动画', '儿童', '喜剧', '犯罪', '纪录', '戏剧',
					'幻想', '黑色', '恐怖', '音乐', '神秘', '浪漫', '科幻', '惊悚', '战争', '西部', '其他'
				],
			};
		},
		methods: {
			onChange(date, dateString) {
				this.movie.releaseTime = dateString;
			},
			async handleOk(e) {	
				this.form.validateFields(async (err, values) => {
					if (!err) {
						this.movie.name = values.name;
						this.movie.playTime = values.playTime;
						this.movie.genre = values.genre;
						this.movie.intro = values.intro;
						this.movie.directors = values.directors;
						this.movie.writers = values.writers;
						this.movie.stars = values.stars;
						console.log(this.movie);
						let {
							data
						} = await this.axios.post(this.requestPath + 'addMovie', this.movie);
						if (data.code == 0) {
							this.newRow.mid = data.data;
							this.newRow.name = this.movie.name;
							this.newRow.genre = [];
							for (var i=0; i<this.movie.genre.length; i++){
								this.newRow.genre.push(this.genreList[this.movie.genre[i]]);
							}
							this.newRow.releaseTime = this.movie.releaseTime;
							this.newRow.directors = this.movie.directors;
							this.$message.success('添加成功');
							this.$emit('updateDataSource', this.newRow);
						}else{
							this.$message.error('添加失败');
						}
					}
				});	
				this.buttonloading = true;
				setTimeout(() => {
					this.$emit('updatePopWinState', false);
					this.buttonloading = false;
				}, 2000);
			},
			handleCancel(e) {
				this.imageUrl = null;
				this.$emit('updatePopWinState', false);
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.movie.pic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	}
</script>

<style lang='scss'>
	.inputFieldStyle {
		width: 320px;
	}
</style>
