<template>
	<div>
		<div>
			<a-button type="primary" icon="plus-circle" @click="showModal">
				添加
			</a-button>
			<MovieAddPage :visible="visible" @updateDataSource="updateDataSource" @updatePopWinState="updatePopWinState"></MovieAddPage>
			<div style="display: flex; justify-content: flex-start;">
				<div>
					搜关键字：
					<a-input style="width:250px; margin: 20px 0 ;" v-model="params.key"
					 placeholder="电影名/导演/编剧/演员" />
				</div>
				<div style="margin: 20px 0 0 25px">
					发行时间区间：
					<a-range-picker @change="onChange" style="width:250px; " :locale="locale"/>
				</div>
				<a-button type="primary" icon="search" style="margin: 20px 0 0 50px" @click="search">
					搜索
				</a-button>
			</div>

			<div v-show="!collapsed">
				电影类别：
				<a-select style="width: 250px;" placeholder="请选择电影类别" v-model="params.genre" :allowClear="true">
					<a-select-option :value=index v-for="(item, index) in genreList">
						{{item}}
					</a-select-option>
				</a-select>
			</div>
			<a-divider style="color: #999999; margin-top: -2px;">
				<a-icon @click="collapsed=!collapsed" :type="collapsed?'down-circle':'up-circle'" />
				{{collapsed?"展开":"收起"}}更多选项
			</a-divider>
		</div>

		<a-table :columns="columns" :data-source="dataSource" :loading="dataloading" :pagination=" {pageSize:pageSize}" :scroll="{ y: 260 }">
			<b slot="name" slot-scope="text">{{ text }}</b>
			<span slot="genre" slot-scope="genre">
				<a-tag v-for="tag in genre" :key="tag" :color="'#' + Math.random().toString(16).substr(2, 6).toUpperCase()">
					{{ tag.toUpperCase() }}
				</a-tag>
			</span>
			<span slot="action" slot-scope="text, record">
				<div style="display: flex; justify-content: center;">
					<a-popconfirm title="是否确定删除？" ok-text="是" cancel-text="否" @confirm="confirm(record.mid)"
					 @cancel="cancel">
						<a>删除</a>
					</a-popconfirm>
					<a-divider type="vertical" />
					<router-view :record="record"></router-view>
				</div>
			</span>
		</a-table>
	</div>

</template>

<script>
	import MovieAddPage from './MovieAddPage.vue'
	import locale from 'ant-design-vue/es/date-picker/locale/zh_CN';
	import {
		mapMutations,
		mapState
	} from "vuex";
	const columns = [
		{
			title: '电影名',
			dataIndex: 'name',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '发行时间',
			dataIndex: 'releaseTime',
		},

		{
			title: '导演',
			dataIndex: 'directors',
		},
		{
			title: '类别',
			dataIndex: 'genre',
			scopedSlots: {
				customRender: 'genre'
			},
		},
		{
			title: '操作',
			dataIndex: 'action',
			scopedSlots: {
				customRender: 'action'
			},
		},
	];
	columns.forEach((r) => {
		r.align = 'center'
	});

	export default {
		components: {
			MovieAddPage
		},
		data() {
			return {	
				locale,
				params : {
					key : null,
					startTime : null,
					endTime: null,
					genre : null,
				},
				dataSource: null,
				page: 1,
				pageSize: 10,
				dataloading: true,
				visible: false,
				columns,
				collapsed: true,
				genreList: ['动作', '冒险', '动画', '儿童', '喜剧', '犯罪', '纪录', '戏剧',
					'幻想', '黑色', '恐怖', '音乐', '神秘', '浪漫', '科幻', '惊悚', '战争', '西部', '其他'
				],
			};
		},
		computed: {
			...mapState(['requestPath'])
		},
		
		async created() {
			this.getAllMovie();
		},
		methods: {
			async getAllMovie(){
				this.dataloading = true;
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allMovies');
					this.dataSource = data.data;
					this.dataloading = false;
				} else {
					console.log("userInfo not found");
				}
			},
			async search(){
				this.dataloading = true;
				let {
					data
				} = await this.axios.post(this.requestPath + 'admin_select', this.params);
				this.dataSource = data.data;
				this.dataloading = false;
			},
			onChange(date, dateString) {
				this.params.startTime = dateString[0];
				this.params.endTime = dateString[1];
			},
			async confirm(e) {
				console.log('del_mid:' + e);	
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'delMovie/' + e);
				if (data.code == 0){
					this.$message.success('删除成功');
					const dataSource = [...this.dataSource];
					this.dataSource = dataSource.filter(item => item.mid !== e);
				}else{
					this.$message.error('删除失败');
				}	
			},
			cancel(e) {
				console.log(e);
			},
			showModal() {
				this.visible = true;
			},
			updatePopWinState(state){
				this.visible = state;
			},
			async updateDataSource(newData){
				console.log(newData);
				const { dataSource } = this;
				this.dataSource = [...dataSource, newData];
				this.getAllMovie();
			},
		},
	};
</script>

<style lang="scss">
</style>
